<template>
  <div class="workspace">
    <div class="label">
      Pokemon:
      <input
        type="text"
        name="pmuid"
        required
        list="pokemon"
        v-model="uid"
      >
      <datalist id="pokemon" v-html="datalistOptions" />
      <input type="text" :value="getName()" readonly disabled>
    </div>

    <div class="label">
      <label>
        Minimum %:
        <select v-html="genIVsOptions()" v-model.number="min_iv" />
      </label>
    </div>

    <div class="label">
      Min IVs (A/D/S):
      <select v-html="genIVOptions()" v-model.number="min_iv_a" />
      <select v-html="genIVOptions()" v-model.number="min_iv_d" />
      <select v-html="genIVOptions()" v-model.number="min_iv_s" />
    </div>

    <div class="label">
      Level:
      <select v-html="genLvOptions()" v-model.number="min_lv" />
      -
      <select v-html="genLvOptions()" v-model.number="max_lv" />
    </div>

    <div class="label">
      <label>
        Language:
        <select v-html="genLangOptions()" v-model.number="lang" />
      </label>
    </div>

    <div class="label">
      <label>
        Trash String:
        <input type="checkbox" v-model="trash">
      </label>
    </div>

    <hr>

    <button @click="copy">Click to Copy!</button>

    <br>

    <textarea id="textarea" v-model="searchString"></textarea>

    <footer>
      this tool fork from <a href="http://ark42.com/pogo/search.php">http://ark42.com/pogo/search.php</a>
    </footer>
  </div>
</template>

<script>
// @ is an alias to /src
import cpm from '@/components/data.cpm.json';
import names from '@/components/data.name.json';
import pms from '@/components/data.pm.json';

pms.forEach((i) => {
  i.uid = i.dex + (i.isotope ? `-${i.isotope}` : '');
});

const genOptions = (v, l = v) => `<option value="${v}" label="${l}"></option>`;

const langs = [
  ['en', 'cp', 'hp', 'English'],
  ['ja', 'cp', 'hp', 'Japanese'],
  ['fr', 'pc', 'pv', 'French'],
  ['es', 'pc', 'ps', 'Spanish'],
  ['de', 'wp', 'kp', 'German'],
  ['il', 'pl', 'ps', 'Italian'],
  ['ko', 'cp', 'hp', 'Korean'],
  ['zh', 'cp', 'hp', 'Chinese'],
  ['pt', 'pc', 'ps', 'Portuguese'],
];

function getMatchingString(a, t) {
  let list = '';
  let last = -1;
  for ( let i = 0; i < a.length; i++ ) {
    if ( a[i] === last + 1 ) {
      list += '-';
      last = a[i];
      while ( ++i < a.length ) {
        if ( a[i] !== last + 1 ) { break; }
        last = a[i];
      }
      if ( a[--i] < 9999 ) {
        list += a[i];
      }
    } else {
      list += ',' + t + a[i];
      last = a[i];
    }
  }

  return list.substr(1);
}

export default {
  name: 'home',
  data() {
    return {
      pms,
      lang: 'zh',
      uid: '1',
      min_iv: 45,
      min_iv_a: 10,
      min_iv_d: 10,
      min_iv_s: 10,
      min_lv: 0,
      max_lv: 34,
      trash: false,
    };
  },

  computed: {
    datalistOptions() {
      return pms.map(pm =>
        genOptions(pm.uid, `#${pm.dex} ${this.getName(pm)}`)
      ).join('');
    },

    searchString() {
      const pm = pms.find(_pm => _pm.uid === this.uid);

      if (!pm) { return 'GG'; }
      const [baseA, baseD, baseS] = [...pm.ads];
      let cps = new Set();
      let hps = new Set();
      const [cpstr, hpstr] = langs.find(l => l[0] === this.lang).slice(1, 3);

      for ( let atk = this.min_iv_a; atk <= 15; atk++ ) {
        for ( let def = this.min_iv_d; def <= 15; def++ ) {
          for ( let sta = this.min_iv_s; sta <= 15; sta++ ) {
            if ( atk + def + sta < this.min_iv ) { continue; }

            for ( let level = this.min_lv; level <= this.max_lv; level++ ) {
              let cp = Math.floor(
                (baseA + atk)
                * Math.sqrt(baseD + def)
                * Math.sqrt(baseS + sta)
                * (cpm[level] * cpm[level] / 10)
              );
              if (cp < 10) { cp = 10; }
              cps.add(cp);

              let hp = Math.floor((baseS + sta) * cpm[level]);
              if (hp < 10) { hp = 10; }
              hps.add(hp);
            }
          }
        }
      }

      if (this.trash) {
        let max = 0;
        for ( let i = 10; i <= 9999; i++ ) {
          if ( cps.has(i) ) {
            max = i;
          }
        }
        for ( let i = 10; i <= max; i++ ) {
          if ( cps.has(i) ) {
            cps.delete(i);
          } else {
            cps.add(i);
          }
        }
      }

      cps = Array.from(cps);
      cps.sort((a, b) => a - b);

      let output = pm.dex + '&' + getMatchingString(cps, cpstr);

      if (!this.trash) {
        hps = Array.from(hps);
        hps.sort((a, b) => a - b);
        output += '&' + getMatchingString(hps, hpstr);
      }

      return output;
    },
  },

  methods: {
    genLangOptions() {
      return langs.map(lang => genOptions(lang[0], lang[3])).join('');
    },
    genLvOptions() {
      return new Array(40)
        .fill(1)
        .map((i, v) => genOptions(v, v + 1))
        .reverse()
        .join('');
    },
    genIVOptions() {
      return new Array(16)
        .fill(1)
        .map((i, v) => genOptions(v))
        .reverse()
        .join('');
    },
    genIVsOptions() {
      return new Array(46)
        .fill(1)
        .map((i, v) => {
          const p = +(v * 100 / 45).toFixed();
          return genOptions(v, p);
        })
        .reverse()
        .join('');
    },
    getName(pm) {
      if (!pm) {
        pm = pms.find(_pm => _pm.uid === this.uid);
        if (!pm) { return ''; }
      }
      const pokedex = `00${pm.dex}`.slice(-3);
      let n = names[pokedex][this.lang] || names[pokedex].en;
      if (pm.isotope) {
        n = `${n} (${pm.isotope})`;
      }
      return n;
    },
    copy() {
      document.getElementById('textarea').select();
      document.execCommand('copy');
    },
  },
};
</script>


<style>
input {
  max-width: 8em;
}
.label {
  display: inline-block;
  display: block;
  margin-bottom: .5em;
  margin-right: 2em;
}

textarea {
  width: 100%;
  height: 50%;
  min-height: 7em;
}

footer {
  text-align: right;
}

* {
  box-sizing: border-box;
}
</style>
